    width: 100vw;
    height: 100vh;
}

#BackgroundTeal {
    
    z-index: -1;
    width: 100vw;
    height: 100vh;
    background: rgb(192,248,216);
}

#HomeTitle {
    position: absolute;
    top: 10vh;
